 */
const tokensCountNum = ref(0);
function tokensCount() {
    if (!clients[clientsIndex.value]) {
        return;
    }
    let count = 0;
    clients[clientsIndex.value].contents.forEach((item) => {
        count += item.tokens;
    });
    tokensCountNum.value = count;
}

